import ACTION_TYPES, { Interfaces } from './actionTypes';
import ASYNC_STATE from '../constants/asyncState';
import { createFetchAction } from '../redux/fetchData';
import * as Models from 'models';

type Articles = {
  [key: string]: Models.Article;
};

export interface State {
  articles: Articles;
  fetchStatus: ASYNC_STATE;
}

export const initialState: State = {
  articles: {},
  fetchStatus: ASYNC_STATE.LOADING,
};

export const getArticles = (state: State): Models.Article[] => {
  const articleIds = Object.keys(state.articles);
  const articles = articleIds.map((id) => {
    return state.articles[id];
  });

  return articles;
};

export const isFetchComplete = (state: State) => {
  return state.fetchStatus === ASYNC_STATE.DONE || state.fetchStatus === ASYNC_STATE.ERROR;
};

export default function articlesReducer(state: State, action: Interfaces.ActionType): State {
  switch (action.type) {
    case ACTION_TYPES.ARTICLES_FETCH_ALL:
      return {
        ...state,
        fetchStatus: ASYNC_STATE.LOADING,
      };
    case ACTION_TYPES.ARTICLES_FETCH_ALL_RESPONSE:
      const articles = action.data.reduce(function (memo: Articles, item: Models.Article) {
        memo[item.id] = item;
        return memo;
      }, {});
      return {
        ...state,
        articles,
        fetchStatus: ASYNC_STATE.DONE,
      };
    case ACTION_TYPES.ARTICLES_FETCH_ALL_ERROR:
      return {
        ...state,
        fetchStatus: ASYNC_STATE.ERROR
      };
    default:
      return state;
  }
}

export function fetchArticles() {
  const action = createFetchAction({
    url: '/articles/list.json',
    initActionType: ACTION_TYPES.ARTICLES_FETCH_ALL,
    successActionType: ACTION_TYPES.ARTICLES_FETCH_ALL_RESPONSE,
    failedActionType: ACTION_TYPES.ARTICLES_FETCH_ALL_ERROR,
  });

  return action;
}
